import { Component } from '@angular/core';
import { MatIconRegistry } from "@angular/material/icon";
import { DomSanitizer } from "@angular/platform-browser";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';

  GAME_LENGTH = 2;

  barva: string = '';
  hodnota: string = '';
  karty: string[] = [];
  stychy = [];
  seznam: Date[] = [];
  opened: boolean = false;
  replay: boolean = false;
  date: Date;
  game: any;

  constructor(private matIconRegistry: MatIconRegistry,
    private domSanitizer: DomSanitizer) {
    this.matIconRegistry.addSvgIcon(
      "menu",
      this.domSanitizer.bypassSecurityTrustResourceUrl("../assets/baseline-menu-24px.svg")
    );
  }

  color(color: string) {
    this.barva = color;
  }

  value(hodnota: string) {
    this.karty.push(hodnota);
    if (this.karty.length % 4 === 0) {
      this.stychy.push(this.karty);
      if (this.stychy.length % this.GAME_LENGTH === 0) {
        let now: number = new Date().getTime();
        localStorage.setItem('' + now, JSON.stringify(this.stychy));
        let index: string = localStorage.getItem('taroky');
        if (index) {
          let indices: number[] = JSON.parse(index);
          indices.push(now);
          localStorage.setItem('taroky', JSON.stringify(indices));
        } else {
          let indices: number[] = [];
          indices.push(now);
          localStorage.setItem('taroky', JSON.stringify(indices));
        }
        this.stychy.push(this.karty);
        this.stychy = [];
      }
      this.karty = [];
    }
    this.barva = '';
  }

  newGame() {
    this.opened = false;
    this.stychy = [];
    this.karty = [];
    this.replay = false;
  }

  replayGame(date: Date) {
    this.opened = false;
    this.replay = true;
    this.date = date;
    let data: string = localStorage.getItem('' + date.getTime());
    if (data) {
      this.game = JSON.parse(data);
    }
  }

  openMenu() {
    this.opened = !this.opened;
    let data: string = localStorage.getItem('taroky');
    if (data) {
      this.seznam = [];
      let hry: number[] = JSON.parse(data);
      for (let hra of hry) {
        this.seznam.push(new Date(hra));
      }
    }
  }
}
